.home-banner {
    background-color: #B5B7BC;
    padding-left: 5%;
    h1 {
        text-align: left;
        font: normal normal 600 48px/60px Source Serif Pro;
        letter-spacing: 0px;
        color: #172026;
        opacity: 1;
    }

    p {
        text-align: left;
        font: normal normal normal 16px/24px Muli;
        letter-spacing: 0px;
        color: #172026;
        opacity: 1;
    }

    button {
        background-color: #1363DF;
        color: #ffff;
        box-shadow: 0px 3px 6px #39373729;
        border: none;
        border-radius: 10px;
        opacity: 1;
    }
}
